<template>
  <div class="course-practise-library-view">
    <head-top title="题库编辑">
      <van-icon class="head-right" name="plus" @click="toAdd"></van-icon>
    </head-top>

    <div class="sub_nav">
      <span>题库名称：{{libraryInfo.name}}
        <van-icon class="operate" name="edit" @click="$router.push('/practise-library/title-edit')"></van-icon>
      </span>
    </div>
    <!--    <div class="sub_nav">-->
    <!--      <span>题数：{{practiseList.length}}</span>-->
    <!--      <span>总分：{{totalScore}}</span>-->
    <!--    </div>-->

    <div class="sub_module" v-if="practiseList && practiseList.length>0">
      <div v-for="(item, index) in practiseList" :key="index" class="van-hairline--bottom practise-item">
        <p class="practise-title">
          <span>{{(index + 1)}}</span>
          <span>{{ '、【'+ practiseTypeOptions[item.type-1].label +'】'+ item.title}} </span>
          <span>（{{item.score}}分）</span>
          <van-icon class="operate" name="edit" @click="toEdit(item)"></van-icon>
          <van-icon class="operate" name="delete" @click="toDrop(item)"></van-icon>
        </p>

        <div v-if="[1, 2].indexOf(item.type) > -1">
          <h4>选项：</h4>
          <p class="practise-options" v-for="(sub, k) in item.options" :key="k">{{sub.value +'、'+sub.label}}</p>
        </div>

        <h4>答案：</h4>
        <div class="practise-content">{{item.answer||'无'}}</div>
        <h4>解析：</h4>
        <div class="practise-content">{{item.analysis||'无'}}</div>
      </div>
    </div>
    <r-lack-data v-else></r-lack-data>

  </div>
</template>

<script>
import { Dialog } from "vant";

export default {
  name: "practiseLibraryView",
  data () {
    return {
      libraryInfo: this.$store.getters.practiseLibrary,
      practiseList: [],
      isLoading: false,

      practiseTypeOptions: [
        { label: "单选题", value: 1 },
        { label: "多选题", value: 2 },
        { label: "判断题", value: 3 },
        { label: "问答题", value: 4 }
      ],
      singleOptions: ["A", "B", "C", "D", "E", "F", "G", "H"],
      judgeOptions: ["对", "错"]
    };
  },
  computed: {
    totalScore () {
      return this.practiseList.reduce((total, a) => {
        return total + parseInt(a.score);
      }, 0);
    }
  },
  mounted () {
    if (this.libraryInfo) {
      this.getLibraryInfo();
    } else {
      this.$router.replace("/practise-library/edit");
    }
  },
  methods: {
    getLibraryInfo () {
      this.isLoading = true;
      this.practiseList = [];
      this.$mainHttp.classPractise.LibraryInfo({
        plid: this.libraryInfo.id
      }).then(res => {
        if (res.Result) {
          // 正序
          res.Datas.sort((a, b) => {
            return a.Key["PSSerial"] > b.Key["PSSerial"] ? 1 : -1;
          });

          res.Datas.forEach(item => {
            this.practiseList.push({
              id: item.Key["PSID"],
              index: item.Key["PSSerial"],
              title: item.Key["PSContent"],
              answer: item.Key["PSAnswer"],
              analysis: item.Key["PSanalysis"],
              type: item.Key["PSType"],
              optionsNum: item.Key["PSOption"],
              score: item.Key["PSScore"],
              state: item.Key["State"],
              options: item.Value.map(sub => {
                return {
                  id: sub["SOID"],
                  value: sub["SOSerial"],
                  label: sub["SOContent"]
                }
              })
            });
          });
        } else if (res.Info) {
          this.$toast(res.Info);
        } else {
        }
        this.isLoading = false;
      }).catch(() => {
        this.isLoading = false;
      });
    },
    toAdd () {
      this.$store.commit("SET_PRACTISE_SUBJECT", {
        id: "",
        index: this.practiseList.length + 1,
        title: "",
        answer: "",
        analysis: "",
        type: 1,
        optionsNum: 4,
        score: 0,
        state: 1,
        options: []
      });
      this.$router.push("/practise-library/exercise-edit");
    },
    toEdit (item) {
      this.$store.commit("SET_PRACTISE_SUBJECT", item);
      this.$router.push("/practise-library/exercise-edit");
    },
    toDrop (item) {
      Dialog.confirm({
        message: "确认删除当前题目？"
      }).then(() => {
        this.toDelete(item);
      }).catch(() => {
      });
    },
    toDelete (item) {
      this.$mainHttp.classPractise.SubjectDelete({
        plid: this.libraryInfo.id,
        psid: item.id
      }).then(res => {
        if (res.Result) {
          this.getLibraryInfo();
          this.$toast("删除成功");
        } else if (res.Info) {
          this.$toast(res.Info);
        } else {
          this.$toast("删除失败");
        }
      }).catch(() => {
        this.$toast("操作失败");
      });
    }
  }
}
</script>

<style lang="less" scoped>
@import "./../../../styles/common/base";
.course-practise-library-view {
  min-height: 100%;
  background-color: #fff;

  .operate {
    padding: 7px;
    font-size: @font-title;
    font-weight: bolder;
  }

  .van-icon-edit {
    color: #3AA0E0;
  }

  .van-icon-delete {
    color: #f56c6c;
  }

  .practise-item {
    padding-top: 5px;
    padding-bottom: 16px;

    .practise-title {
      font-size: @font-title * 0.8;
    }

    h4 {
      font-size: @font-title * 0.9;
      padding-left: 10px;
      position: relative;

      &:before {
        content: " ";
        position: absolute;
        top: 50%;
        left: 0;
        width: 5px;
        height: 16px;
        background: #00cbab;
        transform: translateY(-50%);
      }
    }

    .practise-options {
      margin-left: 16px;
    }

    .practise-content {
      margin-left: 16px;
      padding: 3px 10px;
      color: #f56c6c;
      background: #f8f8f8;
    }
  }
}
</style>
